.dashboard-layout {
    min-height: 100vh;
}

.dashboard-layout .userBtn {
    float: right;
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    transition: color .3s;
}

.dashboard-layout .userBtn:hover {
    color: #1890ff;
}

.dashboard-layout .logo {
    height: 32px;
    background: rgba(255,255,255,.2);
    margin: 16px;
}
